@import url("https://cdn.jsdelivr.net/npm/@xz/fonts@1/serve/hk-grotesk.min.css");

.pw-about {
    background-color: #ebebeb;
}


.pw-about_container {
    display: flex;
    text-align: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 80px;
    gap: 5vw;
}

.pw-active {
    width: 60px;
    height: 80px;
    background-color: #54706b;
    opacity: 0.95;
    float: right;
    margin-right: 210px;
    margin-top: -80px;
}

@media (max-width: 768px) {
    .pw-about_container {
        width: 100%;
        padding-left: 20px;
        padding-right: 20px;
    }

    .pw-active {
        width: 45px;
        height: 30px;
        background-color: #54706b;
        opacity: 1;
        margin-right: 135px;
        margin-top: -41px;
    }
}

.year-container h1 {
    font-family: 'HK Grotesk';
    font-style: normal;
    font-weight: 700;
    font-size: 200px;
    line-height: 160px;
    letter-spacing: -0.55px;
    margin-top: 0px;
    margin-bottom: 0px;
    color: #2D2D2D;
}

.pw-about-container {
    text-align: left;
}

.pw-about-container h1 {
    margin-top: 0px;
    margin-bottom: 20px;
    margin-right: 0px;
    font-size: 100px;
    line-height: 120px;
    max-width: 500px;
}

.pw-about-container p {
    max-width: 600px;
}